"use client";
import axios from "axios";
import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import { Toaster, toast } from "react-hot-toast";

export default function OrderDashboard() {
  const router = useRouter();
  const [orders, setOrders] = useState([]);
  const [filterStatus, setFilterStatus] = useState("all");
  const [filterPayment, setFilterPayment] = useState("all");
  const [selectedOrder, setSelectedOrder] = useState(null);
  const [showModal, setShowModal] = useState(false);
  const [showFilters, setShowFilters] = useState(false);
  const [showMenu, setShowMenu] = useState(false);
  const [showDeleteConfirm, setShowDeleteConfirm] = useState(false);
  const [userName, setUserName] = useState("");
  const [modalData, setModalData] = useState({
    paymentStatus: "",
    remainingAmount: 0,
    paidAmount: 0,
    status: "",
  });

  const getOrders = async () => {
    try {
      const res = await axios.get("/api/orders?action=active");
      if (res.data.success) {
        setOrders(res.data.data);
        console.log("Fetched Orders:", res.data.data);
      } else {
        setOrders([]);
      }
    } catch (error) {
      console.error("Error fetching orders:", error);
      setOrders([]);
    }
  };

  useEffect(() => {
    const user = localStorage.getItem("user2");
    if (!user) {
      router.push("/auth");
      return;
    }
    try {
      const userData = JSON.parse(user);
      setUserName(userData.name || "User");
    } catch (error) {
      console.error("Error parsing user data:", error);
      setUserName("User");
    }
    getOrders();
  }, []);

  const handleLogout = () => {
    localStorage.clear();
    toast.success("Logged out successfully!");
    setShowMenu(false);
    router.push("/auth");
  };

  // Filter orders by both status and payment
  const filteredOrders = orders.filter((order) => {
    const statusMatch = filterStatus === "all" || order.status === filterStatus;
    const paymentMatch =
      filterPayment === "all" || order.paymentStatus === filterPayment;
    return statusMatch && paymentMatch;
  });

  // Open modal
  const handleEditOrder = (order) => {
    setSelectedOrder(order);
    setModalData({
      paymentStatus: order.paymentStatus,
      remainingAmount: order.remainingAmount,
      status: order.status,
    });
    setShowModal(true);
  };

  // Update order
  const handleUpdateOrder = async () => {
    const updatePayload = {
      action: "update",
      orderId: selectedOrder._id,
      updateData: {
        paymentStatus:
          selectedOrder.remainingAmount - modalData.remainingAmount === 0
            ? "paid"
            : modalData.paymentStatus,
        paidAmount:
          modalData.paymentStatus === "paid"
            ? selectedOrder.totalPrice
            : selectedOrder.paidAmount + modalData.remainingAmount,
        remainingAmount:
         modalData.paymentStatus === "paid"
            ? 0
            :
          selectedOrder.remainingAmount - modalData.remainingAmount,
        status: modalData.status,
      },
     

    };
     console.log("Update Payload:", updatePayload);
    try {
      const response = await axios.put("/api/orders", updatePayload);
      if (response.data.success) {
 
        setOrders(
          orders?.map((order) =>
            order._id === selectedOrder._id
              ? {
                  ...order,
                  paymentStatus: updatePayload.updateData.paymentStatus,
                  paidAmount: updatePayload.updateData.paidAmount,
                  remainingAmount: updatePayload.updateData.remainingAmount,
                  status: updatePayload.updateData.status,
                }
              : order
          )
        );
        console.log("Order updated on server:", response.data.order);
        const getUpdatedOrder = response.data.order;
        let finalStatus;
        if (getUpdatedOrder.status === "in-transit"){
          finalStatus = "order-in-transit";
        }
        else if (getUpdatedOrder.status === "completed"){
          finalStatus = "order-delivered";
        }
       else{
        finalStatus = "pending"
       }
       localStorage.setItem("receiptType", finalStatus);
       localStorage.setItem("currentOrder", JSON.stringify(getUpdatedOrder));
        toast.success("Order updated successfully!");
        setShowModal(false);
        router.push("/receipt");
      }
    } catch (error) {
      toast.error("Failed to update order. Please try again.");
      console.error("Error updating order:", error);
    }
  };

  // Delete order
  const handleDeleteOrder = async () => {
    try {
      const response = await axios.put("/api/orders", {
        action: "delete",
        orderId: selectedOrder._id,
      });

      if (response.data.success) {
        // Remove from UI
        setOrders(orders.filter((order) => order._id !== selectedOrder._id));

        toast.success("Order marked as deleted!");
        setShowDeleteConfirm(false);
        setShowModal(false);
      }
    } catch (error) {
      toast.error("Failed to delete order.");
      console.error("Delete error:", error);
    }
  };

  // Get status badge color
  const getStatusColor = (status) => {
    switch (status) {
      case "pending":
        return "bg-yellow-100 text-yellow-700";
      case "in-transit":
        return "bg-blue-100 text-blue-700";
      case "completed":
        return "bg-green-100 text-green-700";
      case "cancelled":
        return "bg-red-100 text-red-700";
      default:
        return "bg-gray-100 text-gray-700";
    }
  };

  const getPaymentColor = (status) => {
    switch (status) {
      case "paid":
        return "bg-green-100 text-green-700";
      case "partially-paid":
        return "bg-yellow-100 text-yellow-700";
      case "unpaid":
        return "bg-red-100 text-red-700";
      default:
        return "bg-gray-100 text-gray-700";
    }
  };

  return (
    <section className="w-full min-h-screen bg-gradient-to-br from-blue-50 to-cyan-50 py-12">
      <Toaster />
      <div className="container mx-auto px-6">
        {/* Header */}
        <div className="mb-8 flex flex-col md:flex-row justify-between items-center gap-4">
          {/* Left - Menu Button & Heading */}
          <div className="flex items-center gap-4">
            <button
              onClick={() => setShowMenu(true)}
              className="bg-blue-600 text-white p-3 rounded-lg hover:bg-blue-700 transition-colors shadow-lg hover:shadow-xl"
            >
              <svg
                className="w-6 h-6"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M4 6h16M4 12h16M4 18h16"
                />
              </svg>
            </button>
            <div>
              <h1 className="text-4xl font-bold text-gray-900 mb-2">
                Order Dashboard
              </h1>
              <p className="text-gray-600">Manage all your orders</p>
            </div>
          </div>
        </div>

        {/* Filter Section */}
        <div className="bg-white rounded-2xl shadow-lg p-6 mb-8">
          <div className="flex flex-col lg:flex-row justify-between items-center gap-4">
            {/* Filter Counts - Left side on desktop, center on mobile */}
            <div className="flex flex-wrap gap-3 justify-center lg:justify-start">
              <div className="px-4 py-2 bg-gray-100 rounded-full">
                <span className="text-sm font-medium text-gray-700">
                  Total: {orders.length}
                </span>
              </div>
              <div className="px-4 py-2 bg-blue-100 rounded-full">
                <span className="text-sm font-medium text-blue-700">
                  Filtered: {filteredOrders.length}
                </span>
              </div>
            </div>

            {/* Filter Dropdown - Center on mobile, right on desktop */}
            <div className="relative w-full lg:w-auto flex justify-center lg:justify-end">
              <button
                onClick={() => setShowFilters(!showFilters)}
                className="bg-blue-600 text-white px-8 py-3 rounded-lg hover:bg-blue-700 transition-colors font-medium shadow-lg flex items-center gap-2 w-full lg:w-64 justify-center"
              >
                Filters
                <span
                  className={`transform transition-transform ${
                    showFilters ? "rotate-180" : ""
                  }`}
                >
                  ▼
                </span>
              </button>

              {/* Dropdown Menu */}
              {showFilters && (
                <div className="absolute top-full mt-2 right-0 lg:right-0 bg-white rounded-xl shadow-2xl p-6 z-50 w-full lg:w-96 max-h-80 overflow-y-auto">
                  {/* Order Status Filters */}
                  <div className="mb-6">
                    <h3 className="text-sm font-semibold text-gray-900 mb-3">
                      Order Status
                    </h3>
                    <div className="space-y-2">
                      <button
                        onClick={() => {
                          setFilterStatus("all");
                          setShowFilters(false);
                        }}
                        className={`w-full text-left px-4 py-2 rounded-lg transition-colors ${
                          filterStatus === "all"
                            ? "bg-blue-600 text-white"
                            : "bg-gray-100 text-gray-700 hover:bg-gray-200"
                        }`}
                      >
                        All Orders ({orders.length})
                      </button>
                      <button
                        onClick={() => {
                          setFilterStatus("pending");
                          setShowFilters(false);
                        }}
                        className={`w-full text-left px-4 py-2 rounded-lg transition-colors ${
                          filterStatus === "pending"
                            ? "bg-yellow-600 text-white"
                            : "bg-gray-100 text-gray-700 hover:bg-gray-200"
                        }`}
                      >
                        Pending (
                        {orders.filter((o) => o.status === "pending").length})
                      </button>
                      <button
                        onClick={() => {
                          setFilterStatus("in-transit");
                          setShowFilters(false);
                        }}
                        className={`w-full text-left px-4 py-2 rounded-lg transition-colors ${
                          filterStatus === "in-transit"
                            ? "bg-blue-600 text-white"
                            : "bg-gray-100 text-gray-700 hover:bg-gray-200"
                        }`}
                      >
                        In Transit (
                        {orders.filter((o) => o.status === "in-transit").length}
                        )
                      </button>
                      <button
                        onClick={() => {
                          setFilterStatus("completed");
                          setShowFilters(false);
                        }}
                        className={`w-full text-left px-4 py-2 rounded-lg transition-colors ${
                          filterStatus === "completed"
                            ? "bg-green-600 text-white"
                            : "bg-gray-100 text-gray-700 hover:bg-gray-200"
                        }`}
                      >
                        Completed (
                        {orders.filter((o) => o.status === "completed").length})
                      </button>
                      <button
                        onClick={() => {
                          setFilterStatus("cancelled");
                          setShowFilters(false);
                        }}
                        className={`w-full text-left px-4 py-2 rounded-lg transition-colors ${
                          filterStatus === "cancelled"
                            ? "bg-red-600 text-white"
                            : "bg-gray-100 text-gray-700 hover:bg-gray-200"
                        }`}
                      >
                        Cancelled (
                        {orders.filter((o) => o.status === "cancelled").length})
                      </button>
                    </div>
                  </div>

                  {/* Payment Status Filters */}
                  <div className="border-t pt-4">
                    <h3 className="text-sm font-semibold text-gray-900 mb-3">
                      Payment Status
                    </h3>
                    <div className="space-y-2">
                      <button
                        onClick={() => {
                          setFilterPayment("all");
                          setShowFilters(false);
                        }}
                        className={`w-full text-left px-4 py-2 rounded-lg transition-colors ${
                          filterPayment === "all"
                            ? "bg-blue-600 text-white"
                            : "bg-gray-100 text-gray-700 hover:bg-gray-200"
                        }`}
                      >
                        All Payments
                      </button>
                      <button
                        onClick={() => {
                          setFilterPayment("paid");
                          setShowFilters(false);
                        }}
                        className={`w-full text-left px-4 py-2 rounded-lg transition-colors ${
                          filterPayment === "paid"
                            ? "bg-green-600 text-white"
                            : "bg-gray-100 text-gray-700 hover:bg-gray-200"
                        }`}
                      >
                        Paid (
                        {
                          orders.filter((o) => o.paymentStatus === "paid")
                            .length
                        }
                        )
                      </button>
                      <button
                        onClick={() => {
                          setFilterPayment("partially-paid");
                          setShowFilters(false);
                        }}
                        className={`w-full text-left px-4 py-2 rounded-lg transition-colors ${
                          filterPayment === "partially-paid"
                            ? "bg-yellow-600 text-white"
                            : "bg-gray-100 text-gray-700 hover:bg-gray-200"
                        }`}
                      >
                        Partially Paid (
                        {
                          orders.filter(
                            (o) => o.paymentStatus === "partially-paid"
                          ).length
                        }
                        )
                      </button>
                      <button
                        onClick={() => {
                          setFilterPayment("unpaid");
                          setShowFilters(false);
                        }}
                        className={`w-full text-left px-4 py-2 rounded-lg transition-colors ${
                          filterPayment === "unpaid"
                            ? "bg-red-600 text-white"
                            : "bg-gray-100 text-gray-700 hover:bg-gray-200"
                        }`}
                      >
                        Unpaid (
                        {
                          orders.filter((o) => o.paymentStatus === "unpaid")
                            .length
                        }
                        )
                      </button>
                    </div>
                  </div>

                  {/* Reset Filters */}
                  <button
                    onClick={() => {
                      setFilterStatus("all");
                      setFilterPayment("all");
                      setShowFilters(false);
                    }}
                    className="w-full mt-4 bg-gray-200 text-gray-700 px-4 py-2 rounded-lg hover:bg-gray-300 transition-colors font-medium"
                  >
                    Reset All Filters
                  </button>
                </div>
              )}
            </div>
          </div>

          {/* Active Filters Display */}
          {(filterStatus !== "all" || filterPayment !== "all") && (
            <div className="mt-4 flex flex-wrap gap-2">
              <span className="text-sm text-gray-600">Active Filters:</span>
              {filterStatus !== "all" && (
                <span className="px-3 py-1 bg-blue-100 text-blue-700 rounded-full text-sm font-medium flex items-center gap-2">
                  Status: {filterStatus}
                  <button
                    onClick={() => setFilterStatus("all")}
                    className="hover:text-blue-900"
                  >
                    ×
                  </button>
                </span>
              )}
              {filterPayment !== "all" && (
                <span className="px-3 py-1 bg-green-100 text-green-700 rounded-full text-sm font-medium flex items-center gap-2">
                  Payment: {filterPayment}
                  <button
                    onClick={() => setFilterPayment("all")}
                    className="hover:text-green-900"
                  >
                    ×
                  </button>
                </span>
              )}
            </div>
          )}
        </div>

        {/* Orders Table */}
        <div className="bg-white rounded-2xl shadow-lg overflow-hidden">
          <div className="overflow-x-auto">
            <table className="w-full">
              <thead className="bg-gray-50">
                <tr>
                  <th className="px-6 py-4 text-left text-sm font-semibold text-gray-700">
                    Order ID
                  </th>
                  <th className="px-6 py-4 text-left text-sm font-semibold text-gray-700">
                    Shop Details
                  </th>
                  <th className="px-6 py-4 text-left text-sm font-semibold text-gray-700">
                    Items(pet)
                  </th>
                  <th className="px-6 py-4 text-left text-sm font-semibold text-gray-700">
                    Total Amount
                  </th>
                  <th className="px-6 py-4 text-left text-sm font-semibold text-gray-700">
                    Payment
                  </th>
                  <th className="px-6 py-4 text-left text-sm font-semibold text-gray-700">
                    Status
                  </th>
                  <th className="px-6 py-4 text-left text-sm font-semibold text-gray-700">
                    Date
                  </th>
                  <th className="px-6 py-4 text-left text-sm font-semibold text-gray-700">
                    Action
                  </th>
                </tr>
              </thead>
              <tbody className="divide-y divide-gray-200">
                {filteredOrders?.map((order, index) => (
                  <tr
                    key={index}
                    className="hover:bg-gray-50 transition-colors"
                  >
                    <td className="px-6 py-4">
                      <span className="font-medium text-gray-900">
                        {"ORD" + String(index + 1).padStart(3, "0")}
                      </span>
                    </td>
                    <td className="px-6 py-4">
                      <div>
                        <p className="font-medium text-gray-900">
                          {order.shopName}
                        </p>
                        <p className="text-sm text-gray-500">
                          {order.shopAddress}
                        </p>
                        <p className="text-sm text-gray-500">{order.phone}</p>
                      </div>
                    </td>
                    <td className="px-6 py-4">
                      <div className="text-sm ">
                        {order?.orderItems.map((item, idx) => (
                          <div key={idx} className="text-gray-700 ">
                            {item.size} × {item.quantity}
                          </div>
                        ))}
                      </div>
                    </td>
                    <td className="px-6 py-4">
                      <span className="font-semibold text-gray-900">
                        Rs. {order.totalPrice}/-
                      </span>
                    </td>
                    <td className="px-6 py-4">
                      <span
                        className={`px-3 py-1 rounded-full text-xs font-medium ${getPaymentColor(
                          order.paymentStatus
                        )}`}
                      >
                        {order.paymentStatus === "paid" && "Paid"}
                        {order.paymentStatus === "partially-paid" && "Partial"}
                        {order.paymentStatus === "unpaid" && "Unpaid"}
                      </span>
                      {order.paymentStatus === "partially-paid" && (
                        <>
                          <p className="text-xs text-gray-500 mt-1">
                            Paid: Rs. {order.paidAmount}/-
                          </p>
                          <p className="text-xs text-gray-500 mt-1">
                            Remaining: Rs. {order.remainingAmount}/-
                          </p>
                        </>
                      )}
                    </td>
                    <td className="px-6 py-4">
                      <span
                        className={`px-3 py-1 rounded-full text-xs font-medium ${getStatusColor(
                          order.status
                        )}`}
                      >
                        {order.status.charAt(0).toUpperCase() +
                          order.status.slice(1).replace("-", " ")}
                      </span>
                    </td>
                    <td className="px-6 py-4 text-sm text-gray-600">
                      {new Date(order.createdAt).toLocaleDateString()}
                    </td>
                    <td className="px-6 py-4">
                      <button
                        onClick={() => handleEditOrder(order)}
                        className="bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700 transition-colors text-sm font-medium"
                      >
                        Manage
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>

          {filteredOrders.length === 0 && (
            <div className="text-center py-12">
              <p className="text-gray-500 text-lg">No orders found</p>
            </div>
          )}
        </div>
      </div>

      {/* Left Sidebar Menu (Offcanvas) */}
      {showMenu && (
        <>
          {/* Backdrop */}
          <div
            className="fixed inset-0 bg-black bg-opacity-50 z-40 transition-opacity"
            onClick={() => setShowMenu(false)}
          ></div>

          {/* Sidebar */}
          <div className="fixed top-0 left-0 h-full w-80  bg-white shadow-2xl z-50 transform transition-transform duration-300 ease-in-out">
            <div className="flex flex-col h-full">
              {/* Header */}
              <div className="bg-gradient-to-r from-blue-600 to-cyan-600 p-6">
                <div className="flex justify-between items-center mb-4">
                  <h2 className="text-2xl font-bold text-white">Menu</h2>
                  <button
                    onClick={() => setShowMenu(false)}
                    className="text-black hover:text-gray-200"
                  >
                    <svg
                      className="w-6 h-6"
                      fill="none"
                      stroke="currentColor"
                      viewBox="0 0 24 24"
                    >
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth={2}
                        d="M6 18L18 6M6 6l12 12"
                      />
                    </svg>
                  </button>
                </div>
                {/* User Info */}
                <div className="flex items-center gap-3">
                  <div className="w-12 h-12 bg-white rounded-full flex items-center justify-center">
                    <svg
                      className="w-6 h-6 text-blue-600"
                      fill="currentColor"
                      viewBox="0 0 20 20"
                    >
                      <path
                        fillRule="evenodd"
                        d="M10 9a3 3 0 100-6 3 3 0 000 6zm-7 9a7 7 0 1114 0H3z"
                        clipRule="evenodd"
                      />
                    </svg>
                  </div>
                  <div>
                    <p className="text-white font-semibold">{userName}</p>
                    <p className="text-blue-100 text-sm">Administrator</p>
                  </div>
                </div>
              </div>

              {/* Menu Items */}
              <nav className="flex-1 p-4 space-y-2">
                <button
                  onClick={() => {
                    router.push("/orderDashboard");
                    setShowMenu(false);
                  }}
                  className="w-full flex items-center gap-3 px-4 py-3 bg-purple-50 text-purple-600 rounded-lg transition-colors group"
                >
                  <svg
                    className="w-5 h-5 text-gray-500 group-hover:text-blue-600"
                    fill="none"
                    stroke="currentColor"
                    viewBox="0 0 24 24"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth={2}
                      d="M9 5H7a2 2 0 00-2 2v12a2 2 0 002 2h10a2 2 0 002-2V7a2 2 0 00-2-2h-2M9 5a2 2 0 002 2h2a2 2 0 002-2M9 5a2 2 0 012-2h2a2 2 0 012 2"
                    />
                  </svg>
                  <span className="font-medium">Manage Orders</span>
                </button>

                <button
                  onClick={() => {
                    router.push("/stocks");
                    setShowMenu(false);
                  }}
                  className="w-full flex items-center gap-3 px-4 py-3 bg-purple-50 text-purple-600 rounded-lg transition-colors group"
                >
                  <svg
                    className="w-5 h-5 text-gray-500 group-hover:text-blue-600"
                    fill="none"
                    stroke="currentColor"
                    viewBox="0 0 24 24"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth={2}
                      d="M20 7l-8-4-8 4m16 0l-8 4m8-4v10l-8 4m0-10L4 7m8 4v10M4 7v10l8 4"
                    />
                  </svg>
                  <span className="font-medium">Manage Stocks</span>
                </button>

                <button
                  onClick={() => {
                    router.push("/rawItems/manageRawItems");
                    setShowMenu(false);
                  }}
                  className="w-full flex items-center gap-3 px-4 py-3 bg-purple-50 text-purple-600 rounded-lg transition-colors group"
                >
                  <svg
                    className="w-5 h-5 text-gray-500 group-hover:text-blue-600"
                    fill="none"
                    stroke="currentColor"
                    viewBox="0 0 24 24"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth={2}
                      d="M19 11H5m14 0a2 2 0 012 2v6a2 2 0 01-2 2H5a2 2 0 01-2-2v-6a2 2 0 012-2m14 0V9a2 2 0 00-2-2H5a2 2 0 01-2-2v2m0 0h10"
                    />
                  </svg>
                  <span className="font-medium">Manage Raw Materials</span>
                </button>

                <button
                  onClick={() => {
                    router.push("/rawItems");
                    setShowMenu(false);
                  }}
                  className="w-full flex items-center gap-3 px-4 py-3 bg-purple-50 text-purple-600 rounded-lg transition-colors group"
                >
                  <svg
                    className="w-5 h-5 text-gray-500 group-hover:text-blue-600"
                    fill="none"
                    stroke="currentColor"
                    viewBox="0 0 24 24"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth={2}
                      d="M19 11H5m14 0a2 2 0 012 2v6a2 2 0 01-2 2H5a2 2 0 01-2-2v-6a2 2 0 012-2m14 0V9a2 2 0 00-2-2H5a2 2 0 01-2-2v2m0 0h10"
                    />
                  </svg>
                  <span className="font-medium">Deleted Order</span>
                </button>
                <button
                  onClick={() => {
                    router.push("/supplierManage");
                    setShowMenu(false);
                  }}
                  className="w-full flex items-center gap-3 px-4 py-3 bg-blue-50 text-blue-600 rounded-lg transition-colors group"
                >
                  <svg
                    className="w-5 h-5"
                    fill="none"
                    stroke="currentColor"
                    viewBox="0 0 24 24"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth={2}
                      d="M17 20h5v-2a3 3 0 00-5.356-1.857M17 20H7m10 0v-2c0-.656-.126-1.283-.356-1.857M7 20H2v-2a3 3 0 015.356-1.857M7 20v-2c0-.656.126-1.283.356-1.857m0 0a5.002 5.002 0 019.288 0M15 7a3 3 0 11-6 0 3 3 0 016 0zm6 3a2 2 0 11-4 0 2 2 0 014 0zM7 10a2 2 0 11-4 0 2 2 0 014 0z"
                    />
                  </svg>
                  <span className="font-medium">Manage Suppliers</span>
                </button>
                 <button
                  onClick={() => {
                    router.push("/transaction");
                    setShowMenu(false);
                  }}
                  className="w-full flex items-center gap-3 px-4 py-3 bg-purple-50 text-purple-600 rounded-lg transition-colors group"
                >
                  <svg
                    className="w-5 h-5"
                    fill="none"
                    stroke="currentColor"
                    viewBox="0 0 24 24"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth={2}
                      d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z"
                    />
                  </svg>
                  <span className="font-medium">Transactions</span>
                </button>

                <div className="border-t border-gray-200 my-4"></div>

                <button
                  onClick={handleLogout}
                  className="w-full flex items-center gap-3 px-4 py-3 text-red-600 hover:bg-red-50 rounded-lg transition-colors group"
                >
                  <svg
                    className="w-5 h-5"
                    fill="none"
                    stroke="currentColor"
                    viewBox="0 0 24 24"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth={2}
                      d="M17 16l4-4m0 0l-4-4m4 4H7m6 4v1a3 3 0 01-3 3H6a3 3 0 01-3-3V7a3 3 0 013-3h4a3 3 0 013 3v1"
                    />
                  </svg>
                  <span className="font-medium">Logout</span>
                </button>
              </nav>

              {/* Footer */}
              <div className="p-4 border-t border-gray-200">
                <p className="text-xs text-gray-500 text-center">
                  OZONE Mineral Water © 2025
                </p>
              </div>
            </div>
          </div>
        </>
      )}

      {/* Modal */}
      {showModal && selectedOrder && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-3xl shadow-2xl max-w-2xl w-full max-h-[90vh] overflow-y-auto">
            <div className="p-8">
              {/* Modal Header */}
              <div className="flex justify-between items-center mb-6">
                <h2 className="text-2xl font-bold text-gray-900">
                  Manage Order - {selectedOrder.id}
                </h2>
                <button
                  onClick={() => setShowModal(false)}
                  className="text-gray-500 hover:text-gray-700 text-2xl"
                >
                  ×
                </button>
              </div>

              {/* Order Details */}
              <div className="mb-6 p-4 bg-gray-50 rounded-xl">
                <h3 className="font-semibold text-gray-900 mb-2">
                  {selectedOrder.shopName}
                </h3>
                <p className="text-sm text-gray-600">
                  {selectedOrder.shopAddress}
                </p>
                <p className="text-sm text-gray-600">
                  {selectedOrder.shopContact}
                </p>
                <div className="mt-3">
                  <p className="text-sm font-medium text-gray-700">Items:</p>
                  {selectedOrder?.orderItems.map((item, idx) => (
                    <p key={idx} className="text-sm text-gray-600">
                      {item.size} × {item.quantity} = Rs.
                      {item.price * item.quantity}/-
                    </p>
                  ))}
                  <p className="text-lg font-bold text-gray-900 mt-2">
                    Total: Rs. {selectedOrder.totalPrice}/-
                  </p>
                </div>
              </div>

              {/* Payment Status */}
              <div className="mb-6">
                <label className="block text-gray-700 font-medium mb-2">
                  Payment Status
                </label>
                <select
                  value={modalData.paymentStatus}
                  onChange={(e) =>
                    setModalData({
                      ...modalData,
                      paymentStatus: e.target.value,
                      remainingAmount:
                        e.target.value === "paid"
                          ? 0
                          : e.target.value === "unpaid"
                          ? selectedOrder.totalPrice
                          : modalData.remainingAmount,
                    })
                  }
                  className="w-full px-4 py-3 rounded-lg border border-gray-300 focus:border-blue-600 focus:outline-none"
                >
                  <option value="unpaid">Unpaid</option>
                  <option value="partially-paid">Partially Paid</option>
                  <option value="paid">Paid</option>
                </select>
              </div>

              {/* Paid Amount (if partially paid) */}
              {modalData.paymentStatus === "partially-paid" && (
                <div className="mb-6">
                  <label className="block text-gray-700 font-medium mb-2">
                    Remaining Amount
                  </label>
                  <input
                    type="number"
                    min="0"
                    max={selectedOrder.totalPrice}
                    value={modalData.remainingAmount}
                    onChange={(e) =>
                      setModalData({
                        ...modalData,
                        remainingAmount: Math.min(
                          parseFloat(e.target.value) || 0,
                          selectedOrder.totalPrice
                        ),
                      })
                    }
                    className="w-full px-4 py-3 rounded-lg border border-gray-300 focus:border-blue-600 focus:outline-none"
                  />
                  <p className="text-sm text-green-600 mt-2 font-medium">
                    Paid: Rs. {selectedOrder.paidAmount}/-
                  </p>
                  <p className="text-sm text-red-600 mt-1 font-medium">
                    Remaining: Rs.{" "}
                    {selectedOrder.remainingAmount - modalData.remainingAmount}
                    /-
                  </p>
                </div>
              )}

              {/* Order Status */}
              <div className="mb-6">
                <label className="block text-gray-700 font-medium mb-2">
                  Order Status
                </label>
                <select
                  value={modalData.status}
                  onChange={(e) =>
                    setModalData({ ...modalData, status: e.target.value })
                  }
                  className="w-full px-4 py-3 rounded-lg border border-gray-300 focus:border-blue-600 focus:outline-none"
                >
                  <option value="pending">Pending</option>
                  <option value="in-transit">In Transit</option>
                  <option value="completed">Completed</option>
                  <option value="cancelled">Cancelled</option>
                </select>
              </div>

              {/* Action Buttons */}
              <div className="flex flex-col sm:flex-row gap-4">
                <button
                  onClick={handleUpdateOrder}
                  className="flex-1 bg-blue-600 text-white py-3 rounded-lg hover:bg-blue-700 transition-colors font-medium"
                >
                  Update Order
                </button>
                <button
                  onClick={() => {
                    setShowModal(false);
                    setShowDeleteConfirm(true);
                  }}
                  className="flex-1 bg-red-600 text-white py-3 rounded-lg hover:bg-red-700 transition-colors font-medium flex items-center justify-center gap-2"
                >
                  <svg
                    className="w-5 h-5"
                    fill="none"
                    stroke="currentColor"
                    viewBox="0 0 24 24"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth={2}
                      d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16"
                    />
                  </svg>
                  Delete Order
                </button>
              </div>

              <button
                onClick={() => setShowModal(false)}
                className="w-full mt-3 bg-gray-200 text-gray-700 py-3 rounded-lg hover:bg-gray-300 transition-colors font-medium"
              >
                Cancel
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Delete Confirmation Modal */}
      {showDeleteConfirm && (
        <div className="fixed inset-0 bg-black bg-opacity-60 flex items-center justify-center z-[60] p-4">
          <div className="bg-white rounded-2xl shadow-2xl max-w-md w-full p-8">
            <div className="text-center mb-6">
              <div className="mx-auto w-16 h-16 bg-red-100 rounded-full flex items-center justify-center mb-4">
                <svg
                  className="w-8 h-8 text-red-600"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z"
                  />
                </svg>
              </div>
              <h3 className="text-2xl font-bold text-gray-900 mb-2">
                Delete Order?
              </h3>
              <p className="text-gray-600">
                Are you sure you want to delete this order? This action cannot
                be undone.
              </p>
            </div>

            <div className="flex gap-4">
              <button
                onClick={() => setShowDeleteConfirm(false)}
                className="flex-1 bg-gray-200 text-gray-700 py-3 rounded-lg hover:bg-gray-300 transition-colors font-medium"
              >
                Cancel
              </button>
              <button
                onClick={handleDeleteOrder}
                className="flex-1 bg-red-600 text-white py-3 rounded-lg hover:bg-red-700 transition-colors font-medium"
              >
                Delete
              </button>
            </div>
          </div>
        </div>
      )}
    </section>
  );
}
